import { getToken, setToken } from '../utils/token'
import api from '../api'
import store from 'store'
import router from '../router'

export default {
  namespaced: true,
  state: {
    menuList: [],
    token: getToken(),
    info: store.get('userInfo') || {
      createAt: 0,
      groupId: 0,
      id: '',
      mail: '',
      nickname: '',
      password: '',
      phone: '',
      userRights: '',
      username: '',
      departmentName: '',
      fullname: 'for test'
    }
  },
  mutations: {
    update (state, data) {
      Object.assign(state, data)
    }
  },
  actions: {
    async login ({ commit, dispatch }, loginInfo) {
      const { key: token } = await api.user.login(loginInfo)

      setToken(token)

      commit('update', {
        token
      })

      dispatch('getUserInfo')
    },
    async getUserInfo ({ commit, state }) {
      // error是由于info中没有真实用户信息
      const info = await api.user.userInfo()

      commit('update', {
        info
      })

      store.set('userInfo', info)
    },
    logout ({ commit, state }) {
      setToken('')
      store.remove('userInfo')
      commit('update', {
        token: ''
      })
      router.replace('/login')
    }
  }
}
